.App {
  text-align: center;
  background-color: blue;
}

.App-logo {
  height: 40vmin;
}

#forStickFooter{
    min-height: calc(100vh - 355px);
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.row{
  display: flex;
  flex-direction: row;
  align-items: center;
}




